.scroll-layer {
  position: absolute;
  top: 50px;
  right: 0;
  width: 6px;
  height: calc(100% - 50px);
  transform: translateX(50%);
  z-index: 10;
}

.scroll-container {
  height: 100%;
  margin-right: -17px;
  overflow-y: scroll;
  overflow-x: hidden;
}

